import React, {PureComponent} from 'react';
import YTSearch from 'youtube-api-search';
import './youtube.scss';
// import youtubeAPI from './youtube-api.js';

export default class Youtube extends PureComponent{
    constructor(props){
        super(props);
        this.state = {
            term: 'rendom',
            videos: []
        }

        YTSearch({key:'AIzaSyBSyGpLXgYYPzOUXpOK0gQ9AkdIZbsvl2w',term: this.state.term},(videos)=>{
            this.setState({...this.state, videos });
            console.log(this.state.videos);
        });
    }

    searchLists = (key = 'rendom') => {
       
    }
    onSearch = ()=> {
        const el = document.getElementById('yt-search-text'); 
        this.setState({ 
            ...this.state,
            term: el.value
        })
    }
    videoItem = () => {
        return this.state.videos.map(video => {
            console.log(video);
            return (
            <li key={video.etag} className="yt-video-item">
                {/* {video.id.videoId} */}
                {/*
                <video controls="true">
                    <source src={`https://www.youtube.com/watch?v=${video.id.videoId}`} />
                </video>
                */}
                <iframe width="420" height="315"
                    src={`https://www.youtube.com/embed/${video.id.videoId}`}>
                </iframe>
                {/* <img src={video.snippet.thumbnails.default.url} /> */}
                <p className="yt-video-title">{video.snippet.title}</p>
            </li>);
        })
    }

    videoViews = (code) => {
        switch (code) {
            case 'list':
                return (
                <div>
                    <ul>{this.videoItem()}</ul>
                </div>
                )
            case 'detail':
                break;
            default:
                break;
        }
    }

    render(){
        this.searchLists();
        return(
            <div className="ty-wrap">
                <div className="sidebar-box">
                    <div className="hamburger-menu">
                        <button />
                    </div>
                </div>
                <div className="video-content">
                    <div className="head-search-box">
                        <div>
                            <h3><img scr="" alt="logo"/> </h3>
                        </div>
                        <div className="v-head-search">
                            <input
                                id="yt-search-text"
                                className="yt-search-input"
                                type="text" 
                                // value={this.state.term}
                                // onChange={e => {this.setState({ term: e.target.value })}}
                            />
                            <button 
                                className="yt-search-btn"
                                onClick={() => this.onSearch()} 
                            />
                        </div>
                        <div>
                            계정
                        </div>
                    </div>
                    <div className="video-list">
                        {this.videoViews('list')}
                    </div>
                </div>
            </div>
        );
    }
}