import React from "react";
import Unity, { UnityContext } from "react-unity-webgl";
import "./pokemon-unity.scss";

export default class App extends React.Component {
    constructor(props) {
      super(props);
  
      this.unityContext = new UnityContext({
        loaderUrl: "pokemonGame/Build/RPG.loader.js",
        dataUrl: "pokemonGame/Build/RPG.data",
        frameworkUrl: "pokemonGame/Build/RPG.framework.js",
        codeUrl: "pokemonGame/Build/RPG.wasm",
        streamingAssetsUrl: "StreamingAssets",
        companyName: "DefaultCompany",
        productName: "TestGame-1",
        productVersion: "1.0",
      });
    }

    render() {
      return (
        <div id="pokemon-screenBox"> 
            <Unity unityContext={this.unityContext} className="pokemon-screen"/>
            <div className="bland"/>
            {
                setTimeout(()=>{
                    document.getElementsByClassName("bland")[0].classList.remove('bland');
                  },3000)
            }
        </div>
      );
    }
  }