import React, { PureComponent } from 'react';
import './painter.scss';

//https://codepen.io/aflutterdev/pen/PoZygbd
export default class Painter extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            canvas: null,
            ctx: null,
            width: 300,
            height: 300,
            color: '#000',
            pos: {
                drawble: false, //클릭 상태 확인
                x: -1,
                y: -1
            }
        };
        this.setColors();
    }

    componentDidMount() {
        const canvas = document.getElementById('painter-boad');
        const ctx = canvas.getContext('2d');
        this.setState({
            ...this.state,
            canvas,
            ctx
        });
        canvas.addEventListener('mousedown', this.listener);
        canvas.addEventListener('mousemove', this.listener);
        canvas.addEventListener('mouseup', this.listener);
        canvas.addEventListener('mouseout', this.listener);

        //http://bencentra.com/code/2014/12/05/html5-canvas-touch-events.html
        canvas.addEventListener('touchstart', this.listener);
        canvas.addEventListener('touchmove', this.listener);
        canvas.addEventListener('touchend', this.listener);
        //draw(canvas);
    }

    listener = (event) => {
        switch (event.type) {
            case 'touchstart':
            case 'mousedown':
                this.initDraw(event);
                break;
            case 'touchmove':
            case 'mousemove':
                if (this.state.pos.drawble)
                    this.draw(event);
                break;
            case 'touchend':
            case 'mouseup':
            case 'mouseout':
                this.finishDraw();
                break;
            default:
                break;
        }
    }

    initDraw = (event) => {
        //그려질 위치
        const { ctx } = this.state;
        ctx.beginPath();
        const coors = this.getPosition(event);
        this.setState({
            ...this.state,
            pos: {
                drawble: true,
                x: coors.x,
                y: coors.y,
            }
        });
        ctx.moveTo(coors.x, coors.y);
    }
    draw = (event) => {
        //그리기 동작
        const { ctx } = this.state;
        const coors = this.getPosition(event);
        this.setState({
            ...this.state,
            pos: {
                ...this.state.pos,
                x: coors.x,
                y: coors.y,
            }
        });
        
        ctx.strokeStyle = this.state.color;
        ctx.lineTo(coors.x, coors.y);
        ctx.stroke();
    }

    finishDraw() {
        //그리지 않을때
        this.setState({
            ...this.state,
            pos: {
                drawble: false,
                x: -1,
                y: -1,
            }
        })
    }

    getPosition = (event) => {
        //그려질 위치지정
        const canvas = this.state.canvas;
        // const x = event.pageX - canvas.offsetLeft; 
        // const y = event.pageY - canvas.offsetTop;
        const x = event.pageX - canvas.getBoundingClientRect().x; //돔 이동시킬때를 위해 실제 위치 다시 계산
        const y = event.pageY - canvas.getBoundingClientRect().y;
        return { x, y };
    }

    setColors = () => {
        const colors = [
            '#000', '#7e7e7e', '#880315', '#ed1d24', '#ff7f28', '#fff200', '#22b14c', '#00a2e8', '#3f48cc', '#a348a4'
            , '#fff', '#c3c3c3', '#b97a56', '#feaec9', '#feca0f', '#efe4b0', '#b5e61e', '#98d9ea', '#7092be', '#c8bfe7'
        ];
        return colors.map((color, index) => {
            return (<li
                key={`color-${index}`}
                onClick={() => {
                    this.setState({
                        ...this.state,
                        color
                    })
                }}
                className="painter-color-box"
                style={{ backgroundColor: color }}
            />)
        });
    }

    onSave = () => {
        if(!window.confirm('현재 그림을 저장 하시겠습니까?')) return false;
        const {canvas}= this.state;
        const dataURL = canvas.toDataURL('image/jpg');
        // dataURL = dataURL.replace(/^data:image\/[^;]*/, 'data:application/octet-stream');
        // dataURL = dataURL.replace(/^data:application\/octet-stream/, 'data:application/octet-stream;headers=Content-Disposition%3A%20attachment%3B%20filename=Canvas.png');
        const aTag = document.createElement('a');
        aTag.download = 'image.jpg';
        aTag.href = dataURL;
        aTag.click();
    }

    onSketchSize = (el) => {
        // console.log(el.target.value);
        this.setState({
            ...this.state,
            width: 300 + (el.target.value * 10),
            height: 300 + (el.target.value * 10),
        })
    }

    render() {
        return (
            <div className="painter-wrap">
                <div className="painter-head">
                    <nav className="painter-options">
                        <div className="painter-ation-box">
                            <ul className="painter-ation">
                                <li className="painter-save" title="save" onClick={() => {this.onSave()}}/>
                                <li className="painter-redo painter-redo-gray" title="redo" />
                                <li className="painter-undo" title="undo" />
                            </ul>
                        </div>
                        <div className="painter-select-color-box">
                            <p className="select-color" style={{backgroundColor: this.state.color}}/>
                            <p>색</p>
                            {/* <p>1</p> */}
                        </div>
                        <div className="painter-colos-box">
                            <ul className="painter-colos">
                                {this.setColors()}
                            </ul>
                        </div>
                        {/* <div className="painter-size-box">
                            <input type="range" min="1" max="30" defaultValue={10} onChange={this.onSketchSize}/>
                        </div> */}
                    </nav>
                    <div className="painter-content">
                        <canvas
                            id="painter-boad"
                            width={this.state.width}
                            height={this.state.height}
                        />
                    </div>
                </div>
            </div>
        );
    }
}