import React, {PureComponent} from 'react';
import './tetris.scss';

export default class Tetris extends PureComponent {
    constructor(props){
        super(props);
        this.state = {
            canvas : null,
            ctx: null,
            dropCounter: 0,
            arena: null,
            player : {
                pos : {x : 5, y: 5},
                matrix: [
                    [0,0,0],
                    [1,1,1],
                    [0,1,0]
                ]
            }
        }
    }

    playerMove = (dir) => {
        const {arena,player} = this.state;
        player.pos.x += dir;
        if(this.collide(arena,player)){
            player.pos.x -= dir;
        }
        this.setState({
            ...this.state,
            player
        })
    }

    //
    componentDidMount(){
       console.log('> componentDidMount');
       const canvas = document.getElementById('tetris-game');
       const ctx = canvas.getContext('2d');
       ctx.scale(20, 20); // context 크기
       this.setState({
            ...this.state,
            canvas,
            ctx,
            arena : this.createMatrix(12,20)
       });
       canvas.addEventListener('keydown', this.listener);
       this.update();
    }

    listener = (event) => {
        const {player} = this.state;
        switch (event.keyCode){
            case 37: this.playerMove(-1); break;
            case 39: this.playerMove(1); break;
            case 40: this.playerDrop(); break;
            case 81: this.playerRotate(1); break;
            default: break;
        }
        this.setState({
            ...this.state,
            player
        })
    };

    draw = () => {
        console.log('> draw');
        let { canvas, ctx, player, arena } = this.state;
        if(!canvas) return;
        ctx.fillStyle = '#000';
        ctx.fillRect(
            0,
            0,
            canvas.width,
            canvas.height
        );
        this.drawMatrix(arena, {x:0, y:0});
        this.drawMatrix(player.matrix, player.pos);
    }

    drawMatrix = (matrix, offset) => {
        console.log('> drawMatrix');
        const {ctx} = this.state;
        if(!ctx) return;
        //offset 차감계산
        matrix.forEach((row, y) => {
            row.forEach((value, x) => {
                if(value !== 0){
                    ctx.fillStyle = 'blue';
                    // fillReact(x 좌표, y 좌표, width, length)
                    ctx.fillRect(x + offset.x , y + offset.y , 1, 1);  
                }
            });
        });
    }

    createMatrix = (w,h) => {
        console.log('> createMatrix');
        const matrix = [];
        while(h--){
            matrix.push(new Array(w).fill(0));
        }
        return matrix;
        
    }

    merge = (arena, player) => {
        console.log('> merge');
        player.matrix.forEach((row,y) => {
            row.forEach((value, x) => {
                if(value !== 0) {
                    arena[y + player.pos.y][x + player.pos.x] = value;
                }
            })
        })
    }

    collide = (arena, player) => {
        const [m,o] = [player.matrix, player.pos];
        for(let y = 0; y < m.length; ++ y){
            for(let x = 0; x < m[y].length; ++x){
                if(m[y][x] !== 0 &&
                    (arena[y + o.y]&
                     arena[y + o.y][x + o.x]) !== 0){
                         return true;
                }
            }
        }
        return false;
    }

    playerRotate = (dir) => {
        const {player, arena} = this.state;
        const pos = player.pos.x;
        let offset = 1;
        this.rotate(player.matrix, dir);
        while(this.collide(arena, player)){
            console.log("1. before pos.x : ", player.pos.x);
            player.pos.x += offset;
            this.setState({
                ...this.state,
                player
            })
            console.log("2. After pos.x : ", player.pos.x);
            offset = - (offset + (offset > 0 ? 1 : -1));
            console.log("3. After offset : ", offset);

            if(offset > player.matrix[0].length){
                this.rotate(player.matrix, -dir);
                player.pos.x = pos;
                return;
            }
        }
    }

    update = (time = 0) => {
        console.log('> update');
        // let dropCounter = 0;
        let dropInterval = 1000;
        let lastTime = 0;
        let {player, dropCounter} = this.state;
        
        const deltaTime = time - lastTime;
        lastTime = time;
        dropCounter += deltaTime;
        if(dropCounter > dropInterval){
            player.pos.y++;
            this.setState({
                ...this.state,
                dropCounter: 0,
                player
            });
        }
        this.draw();
        requestAnimationFrame(this.update);
    }

    playerDrop = () => {
        console.log('> playerDrop');
        const {player} = this.state;
        // player.pos.y++;
        player.pos.y--;
        this.setState({
            ...this.state,
            player,
            dropCounter: 0
        });
    }

    rotate = (matrix, dir) => {
        for(let y = 0; y < matrix.length; ++y){
            for(let x = 0; x<y; ++x){
                [
                    matrix[x][y],
                    matrix[y][x],
                ] = 
                [
                    matrix[y][x],
                    matrix[x][y],
                ]
            }
        }
        if(dir > 0){
            matrix.forEach(row => row.reverse());
        }else {
            matrix.reverse();
        }
    }
    // this.update();
    render(){
        console.log('> render');
        return(
            <div>
                <canvas id="tetris-game" width="240" height="400" />
            </div>
        );
    }
}
