import React, { Component, Fragment } from 'react'
import './games.scss'
import gameList from './gameList.js';
// import * as actions from '../../actions';

class Games extends Component {
    constructor(props) {
        super(props);
        this.state = {
            startGame:null,
            games:[
                //http://icongal.com/search/pokemon
                //https://imgur.com/gallery/Kvwvz
                {
                    name: 'Pokemon',
                    title: '포켓몬스터',
                    hidden: false,
                    focus: false,
                    select: false,
                    price: 0,
                    // image: 'https://mblogthumb-phinf.pstatic.net/MjAyMDAxMjVfMTE3/MDAxNTc5OTU2NTE0OTU2.AnopmGVHzgi8GRnad0rgRoJLY4IO-QcM-cs75lO50WEg.i3X5oepfC3vuDWsm0e9MzgTIC3D45gK5JYKzxOCmJqsg.GIF.7mperial/포켓몬스터_골드_한글판_다운(1).gif?type=w800',
                    imageAnime: 'https://i.gifer.com/T2a.gif',
                    imageStatick : 'https://img1.daumcdn.net/thumb/R800x0/?scode=mtistory2&fname=https%3A%2F%2Ft1.daumcdn.net%2Fcfile%2Ftistory%2F2706294F582C55BC0A',
                },
                {
                    name: 'Mario',
                    title: '슈퍼마리오',
                    hidden: true,
                    focus: false,
                    select: false,
                    price: 0,
                    imageAnime: 'https://i2.wp.com/www.invajy.com/wp-content/uploads/2018/05/SuperMarioBrosGIF-1.gif?fit=619%2C580&ssl=1',
                    imageStatick: 'https://cdn02.nintendo-europe.com/media/images/10_share_images/games_15/virtual_console_nintendo_3ds_7/SI_3DSVC_SuperMarioBros.jpg',
                },
                {
                    name: 'Tetris',
                    title: '테트리스',
                    hidden: true,
                    focus: false,
                    select: false,
                    price: 0,
                    imageAnime: 'https://i2.wp.com/www.invajy.com/wp-content/uploads/2018/05/SuperMarioBrosGIF-1.gif?fit=619%2C580&ssl=1',
                    imageStatick: 'https://cdn02.nintendo-europe.com/media/images/10_share_images/games_15/virtual_console_nintendo_3ds_7/SI_3DSVC_SuperMarioBros.jpg',
                },
            ]
        }
    }
    onAllGameList(){
        return this.state.games.map((game,index) => {
            return (
                <li 
                    key={game.name} 
                    className={`game-item ${!game.focus || 'game-focus'} ${!game.select || 'game-select'}`}
                    onMouseEnter={() => this.onFocusGame(index, true)}
                    onMouseLeave={() => this.onFocusGame(index, false)}
                    onClick={() => this.selectGame(index)}
                    >
                    <div className="game-box">
                        <p
                            className="game-image"
                            style={{backgroundImage: `url(${game.focus ? game.imageAnime : game.imageStatick})`}}
                            />
                        <p>{game.title}</p>
                    </div>
                </li>
            );
        });
    }
    onFocusGame = (i,isFocus) => {
        const games = this.state.games;
        games[i].focus = isFocus;
        this.setState({
            ...this.state,
            games
        })
    }
    selectGame = (i) => {
        const games = this.state.games;
        games[i].focus = false;
        games[i].select = true;
        this.setState({
            ...this.state,
            startGame: games[i].name,
            games
        });
        // this.startGame(games[i].name);
    }
    startGame = () => {
        const gameName = this.state.startGame;
        if(!gameName){ 
            return (<Fragment>
                <div className="games-wrap">
                    <h3>Game List</h3>
                    <ul>
                        {this.onAllGameList()}
                    </ul>
                </div>
            </Fragment>);
        }else return gameList[gameName];
    }

    render(){
        return(
            this.startGame()
        )
    }
}

export default Games;